import { limit } from '../../constants/const.js'
export default {
	data() {
		return {
			type: 'pratice',
			corrects: 0,
			limit
		};
	},
	computed: {
		score() {
			return 100 / limit * this.corrects
		}
	},
	methods: {
		jump(url) {
			uni.reLaunch({
				url,
			})
			
		}
	},
	onLoad(opt) {
		this.corrects = opt.corrects
		this.type = opt.type
	}
}
